@import "src/styles/variables";
@import "src/styles/mixins";

.input {
    margin: rem(15) 0;
    width: rem(250);
    background-color: whitesmoke;
    border-radius: rem(3);
    box-shadow: 1px 2px 3px gray;
}
.buttonsPanel {
  display: flex;
  justify-content: space-between;
  width: rem(100);
  padding: rem(15) 0;
  margin: 0 auto;
}

.onError {
    font-weight: bold;
    color: red;
}

.dropdowns {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: rem(15);
}

$dropdown--font    : $Quicksand;
$dropdown--bgColor : whitesmoke;


.dropdown {
  display: flex;
  flex-direction: column;
  font-family: $dropdown--font;
  font-size: rem(15);
  text-align: left;
  position: relative;
  background-color: transparent;
  width: 40%;
  border-radius: rem(4);

  &:after {
    @include addMaterialAfter('expand_more');
    font-size: rem(25);
    top: unset;
    bottom: rem(13);
    opacity: .5;
  }
}

  .label {
    font-size: rem(18);
    font-weight: bold;

    padding-bottom: rem(4);
    padding-left: rem(4);
  }

  .select {
    /*убирает стрелочку из выпадающего списка*/
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: none; //чтобы select не подсвечивался по умолчанию

    font-family: $dropdown--font;
    box-sizing: border-box;
    background: $dropdown--bgColor;
    font-size: rem(15);
    line-height: rem(20);
    border: solid {
      color: $black-50;
      radius: rem(4);
      width: 2px;
    }
    padding: rem(13) rem(35) rem(15);
    width: 100%;

    transition-property: border;
    transition-duration: .3s;

    &:focus, &:hover {
      border: solid $black-75 2px;
    }

    &:active {
    }

    &:after {
      @include addMaterialAfter('expand_more');
      font-size: rem(25);
      top: unset;
      bottom: rem(13);
    }
  }

